.testArea {
  top: 0;
  position: absolute;
  height: 100vh;
  width: 100%;
  z-index: 20;
  pointer-events: none;
}

.swiper-button-prev {
  position: absolute;
  color: white;
  pointer-events: all;
}
.swiper-button-prev::after {
  position: absolute;
  color: white;
  pointer-events: all;
  top: 39vh;
  padding: 20px;
}
.swiper-button-next {
  position: absolute;
  top: 39vh;
  color: white;
  pointer-events: all;
}
.swiper-button-next::after {
  position: absolute;
  top: 39vh;
  padding: 20px;
  color: white;
  pointer-events: all;
}

.trollImage {
  height: 80%;
  width: 29.3%;
  background-color: yellow;
  background-image: url(../Carousel/20220322_234906078_iOS.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}

.container {
  height: 100vh;

  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

/*  */

.carousel {
  pointer-events: none;
  top: 0;
  position: absolute;
  height: 100vh;

  width: 100%;
  z-index: 20;
  overflow: hidden;
  /* background-color: blue; */
}
.inner {
  white-space: nowrap;
  transition: transform 0.3s;
}

.carousel-item {
  pointer-events: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 100vh;

  color: #fff;
  z-index: 21;
  /* background-color: green; */
}

.indicators {
  pointer-events: all;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  z-index: 22;
  display: flex;
  justify-content: center;
  align-items: center;
}

.indicators > button {
  cursor: pointer;
  margin: 5px;
}
.indicators > button.active {
  background-color: green;
  margin: 5px;
}
